import React, { useEffect, useMemo } from "react";
import { useSEO } from "../context/SEOContext";
import { SEOHeadProps, MetaTag } from "../types";
import { generateMetaTags, formatJsonLd } from "../utils/helpers";

/**
 * Component to generate and inject SEO tags into the document head
 */
const SEOHead: React.FC<SEOHeadProps> = ({ additionalElements }) => {
  const { defaultSEO, currentSEO } = useSEO();

  // Use current SEO or default SEO if current is not set
  const activeSEO = currentSEO || defaultSEO;

  // Memoize meta tags to avoid unnecessary re-renders
  const metaTags = useMemo(() => generateMetaTags(activeSEO), [activeSEO]);

  // Memoize JSON-LD script to avoid unnecessary re-renders
  const jsonLdScript = useMemo(() => {
    if (!activeSEO.jsonLd) return null;

    try {
      return typeof activeSEO.jsonLd === "string"
        ? activeSEO.jsonLd
        : JSON.stringify(activeSEO.jsonLd);
    } catch (error) {
      console.error("Invalid JSON-LD data:", error);
      return null;
    }
  }, [activeSEO.jsonLd]);

  // Set the language attribute on the <html> tag
  useEffect(() => {
    const language = activeSEO.language || "en"; // Default to "en"
    if (document.documentElement.lang !== language) {
      document.documentElement.lang = language;
    }
  }, [activeSEO.language]);

  return (
    <>
      {/* Title tag */}
      <title>{activeSEO.title || "Default Title"}</title>

      {/* Canonical URL */}
      {activeSEO.canonical && (
        <link rel="canonical" href={activeSEO.canonical} />
      )}

      {/* Meta tags */}
      {metaTags
        .filter((tag) => tag.content) // Ensure content is not empty
        .map((tag: MetaTag, index: number) => {
          const metaProps: Record<string, string> = {
            key: `meta-${index}`,
            content: tag.content,
          };

          if (tag.name) metaProps.name = tag.name;
          if (tag.property) metaProps.property = tag.property;
          if (tag.httpEquiv) metaProps["http-equiv"] = tag.httpEquiv;

          return <meta {...metaProps} />;
        })}

      {/* JSON-LD structured data */}
      {jsonLdScript && (
        <script
          type="application/ld+json"
          dangerouslySetInnerHTML={{ __html: jsonLdScript }}
        />
      )}

      {/* Additional elements */}
      {additionalElements}
    </>
  );
};

export { SEOHead };
export default SEOHead;
