import React, { useEffect, useMemo } from "react";
import { useSEO } from "../context/SEOContext";
import { PageSEOProps, SEOMetadata } from "../types";
import { deepMerge } from "../utils/helpers";

/**
 * Component to set SEO metadata for a specific page
 */
const PageSEO: React.FC<PageSEOProps> = ({ seo, merge = true }) => {
  const { defaultSEO, updateCurrentSEO, clearCurrentSEO } = useSEO();

  // Memoize the merged SEO to avoid unnecessary recalculations
  const pageSEO = useMemo(() => {
    return merge ? deepMerge(defaultSEO, seo) : seo;
  }, [defaultSEO, seo, merge]);

  useEffect(() => {
    // Update current SEO
    updateCurrentSEO(pageSEO);

    // Clean up when component unmounts
    return () => {
      clearCurrentSEO();
    };
  }, [pageSEO, updateCurrentSEO, clearCurrentSEO]);

  // This component doesn't render anything
  return null;
};

export { PageSEO };
export default PageSEO;
